class FileList extends Component{
  constructor(){
    super();
    this.state = {
      files: []
    };
  }

  handleDrop(acceptedFiles){
    this.setState({
      files: acceptedFiles,
    })
  }

  handleSubmit(e){
    e.preventDefault();
    const { caseId } = this.props;
    const { files } = this.state;
    if(!files || files.length === 0)
      return;
    let data = new FormData();
    data.append('caseid', caseId);
    data.append('description', "");
    data.append('file', files[0]);
    $.ajax({
      url: 'index/upload',
      method: 'POST',
      data,

      cache: false,
      processData: false,
      contentType: false,
    }).done(data => {
      console.log(data);
    })
  }

  render(){
    let { fileList } = this.props;
    return (
      <div style={{width: "50%", padding: "0 15px", display: "inline-block"}}>
        <h4 style={{margin: "0 0 20px 20px"}}></h4>
        <ul>
          <li style={{margin: "0 0 20px 20px", color: "#3f8cbc"}}><a href={``} target="_blank"><img src="./asset/image/file-lock.png" width="30px"/></a></li>
          {fileList && fileList.map((f, i) => <li style={{margin: "0 0 20px 20px", color: "#3f8cbc"}} key={i}><a href={`/download?fileid=${f.fileid}`} target="_blank">{i + 1}</a></li>)}
        </ul>
        <List>
          <ListItem
            leftAvatar={<Avatar icon={<ActionAssignment/>} backgroundColor={blue500}/>}
            primaryText="Text"
          />
        </List>
        <Dropzone
          onDrop={acceptedFiles => this.handleDrop(acceptedFiles)}
          style={{
            width: "300px",
            height: "120px",
            border: "3px dashed #f2f2f2",
            borderRadius: "6px",
            color: "#E2E2E2",
            textAlign: "center",
            lineHeight: "120px",
            fontSize: "19px",
          }}
        ></Dropzone>
        <RaisedButton
          label="" labelColor="#FFFFFF"
          backgroundColor="#3F8CBC" onClick={e => this.handleSubmit(e)}
        />
      </div>
    );
  }
}

let CaseDetailTable = ({caseData}) => {
  return (
    <div className="case-detail-table">
      <div className="clearfix">
        <div className="title"></div>
        <div className="content">{modal.toLocal(caseData.status, "ch")}</div>
      </div>
      <div className="clearfix">
        <div className="title"></div>
        <div className="content">{caseData.seller}</div>
      </div>
      <div className="clearfix">
        <div className="title"></div>
        <div className="content">{caseData.price}</div>
      </div>
    </div>
  );
};

let CaseAction = ({status, id, seller, isCommented, clientName, uid, fileList}) => {
  let onSchedule = () => modal.invokeScheduleModal(id, seller, "");
  let onSign = () => modal.invokeSignatureModal(id, uid, fileList);
  let onClose = () => modal.invokeCancelModal(id);
  let onReview = () => modal.invokeReviewModal(id, seller, clientName);
  let onMessage = () => modal.invokeMessageModal(id);
  switch (status) {
    case "inspect":
      return (
        <div className="action-container">
          <button className="regular-btn-theme" onClick={null} style={{display: "none"}}></button>
          <button className="regular-btn-theme disabled" onClick={onClose}></button>
        </div>);
    case "accept":
      return (
        <div className="action-container">
          <button className="regular-btn-theme warning" onClick={onSign}></button>
          <button className="regular-btn-theme" onClick={onMessage} style={{display: "none"}}></button>
          <button className="regular-btn-theme disabled" onClick={onClose}></button>
        </div>
      );
    case "complete":
      if (!isCommented)
        return (
          <div className="action-container">
            <button className="regular-btn-theme done" onClick={onReview}></button>
          </div>
        );
  }
  return <div/>;
};

let CaseProcessBar = ({status}) => {
  switch (status) {
    case "inspect":
      return <CaseProcessInspect/>;
    case "accept":
      return <CaseProcessAccept/>;
    case "complete":
      return <CaseProcessComplete />;
    default:
      return <CaseProcessDefault />;
  }
};

